<template>
    <div class="business">
      <div class="full-width business-banner" :style="{backgroundImage:`url(${img})`}">
      </div>
      <div class="container crumbs">
        您当前的位置 ：招商中心  >>  {{tab[tabActive]}}
      </div>
      <div class="full-width tab">
        <div class="container">
          <div class="row" style="width: 600px">
            <div class="row-item" v-for="(i,n) in tab">
              <a @click="tabActive=n" :class="{active:tabActive===n}">{{i}}</a>
            </div>
          </div>
        </div>
      </div>
      <transition name="fade" mode="out-in">
        <div key="1" v-if="tabActive === 0" class="full-width card">
          <div class="container">
            <div class="row">
              <div class="business-card" v-for="i in card">
                <div class="blue-card">
                  <span>{{i.tile}}</span>
                  <img
                    src="/static/business/business-CooperativeAdvantage-bg.png"
                    alt="">
                </div>
                <img class="icon_off" :src="i.icon_off" alt="">
                <img class="icon_on" :src="i.icon_on">
                <img class="bg" :src="i.img" alt="">
                <div class="mask">
                  <span>{{i.content}}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div key="2" v-if="tabActive === 1" class="full-width" style="background-color: #f2f2f2">
          <div class="container advantage">
            <div class="adv-l" v-for="(i,n) in advantage" :class="{reverse:n===1}">
              <img class="ico" :src="i.ico" alt="">
              <div class="adv-inf">
                <span class="tile">{{i.tile}}</span>
                <div class="adv" v-for="j in i.adv">
                  {{j}}
                </div>
              </div>
              <img class="big" :src="i.big" alt="">
            </div>
          </div>
        </div>
        <div key="3" v-if="tabActive === 2" class="full-width" style="background-color: #f2f2f2">
          <div class="container join">
            <div class="join-bg"><img src="/static/business/business-join-photo1.png" alt=""></div>
            <form class="join-form">
              <table cellspacing="0" cellpadding="0" width="528">
                <tr>
                  <td class="tile">公司全称:</td>
                  <td class="input">
                    <input type="text" placeholder="请输入公司全称">
                  </td>
                </tr>
                <tr>
                  <td class="tile" style="vertical-align: top">公司地址:</td>
                  <td class="input">
                    <div class="address-select">
                      <select name="province" id="province">
                        <option value="">四川</option>
                        <option value="">湖北</option>
                        <option value="">陕西</option>
                        <option value="">湖南</option>
                      </select>
                      <select name="city" id="city">
                        <option value="">成都</option>
                        <option value="">重庆</option>
                        <option value="">苏州</option>
                        <option value="">北京</option>
                      </select>
                      <select name="town" id="town">
                        <option value="">沙坪坝</option>
                        <option value="">九龙坡</option>
                        <option value="">渝北</option>
                        <option value="">江北</option>
                      </select>
                    </div>
                    <input type="text" placeholder="请输入详细地址">
                  </td>
                </tr>
                <tr>
                  <td class="tile">您的姓名：</td>
                  <td class="input">
                    <input type="text" placeholder="请输入您的姓名">
                  </td>
                </tr>
                <tr>
                  <td class="tile">您的电话：</td>
                  <td class="input">
                    <input type="text" placeholder="请输入您的姓名">
                  </td>
                </tr>
                <tr>
                  <td class="tile" style="vertical-align: top">留言:</td>
                  <td class="input">
                    <textarea  id="" cols="30" rows="10"></textarea>
                  </td>
                </tr>
                <tr>
                  <td class="tile"></td>
                  <td class="input">
                    <a class="btn">提交申请</a>
                  </td>
                </tr>
              </table>
            </form>
          </div>
        </div>
      </transition>
    </div>
</template>

<script>

    export default {
        name: "business",
        data(){
          return{
            tabActive:0,
            img:'/static/detail/3.png',
            tab:['合作优势','我们的优势','加入我们'],
            card:[
              {
                img: '/static/business/business-CooperativeAdvantage-photo1.png',
                tile:'零投入 高回报',
                icon_off:'/static/business/business-CooperativeAdvantage-icon1-default.png',
                icon_on:'/static/business/business-CooperativeAdvantage-icon1-hover.png',
                content:'无合作门槛，0推广费用，借助officemate的客户资源，提供服务，获取回报'
              },
              {
                img: '/static/business/business-CooperativeAdvantage-photo2.png',
                tile:'优质资源 稳定收益',
                icon_off:'/static/business/business-CooperativeAdvantage-icon2-default.png',
                icon_on:'/static/business/business-CooperativeAdvantage-icon2-hover.png',
                content:'officemate从业多年，累计服务政府及企业30万+，获得了大量优质高粘性客户'
              },
              {
                img: '/static/business/business-CooperativeAdvantage-photo3.png',
                tile:'品牌塑造 增强实力',
                icon_off:'/static/business/business-CooperativeAdvantage-icon3-default.png',
                icon_on:'/static/business/business-CooperativeAdvantage-icon3-hover.png',
                content:'服务于大型知名企业，提升自身企业的知名度，不断提升服务能力，增强企业实力'
              },
              {
                img: '/static/business/business-CooperativeAdvantage-photo4.png',
                tile:'公平公正 良性竞争',
                icon_off:'/static/business/business-CooperativeAdvantage-icon4-default.png',
                icon_on:'/static/business/business-CooperativeAdvantage-icon4-hover.png',
                content:'限量筛选行业优质服务商，保证资源充分匹配，抵制同质化的低价竞争'
              }
            ],
            advantage:[
              {
                ico:'/static/business/business-advantage-icon1.png',
                big:'/static/business/business-advantage-photo1.png',
                tile:'海量客户资源',
                adv:[
                  '集团客户300000+',
                  '合作伙伴1000+',
                  '加盟商客户200+'
                ]
              },
              {
                ico:'/static/business/business-advantage-icon2.png',
                big:'/static/business/business-advantage-photo2.png',
                tile:'立体化营销体系',
                adv:[
                  '五线三通，客户共享',
                  '全国布局，深耕行业'
                ]
              },
              {
                ico:'/static/business/business-advantage-icon3.png',
                big:'/static/business/business-advantage-photo3.png',
                tile:'塑造品牌价值',
                adv:[
                  '精准直达，提升曝光',
                  '品牌备述，增强实力'
                ]
              }
            ]
          }
        },
        created(){
          let data = {
            top:true,
            nav:true,
            foot:true
          };
          this.$store.dispatch('layout/setLayout', data);
        }
    }
</script>

<style scoped lang="stylus">
.full-width.business-banner
  height 600px
  background center no-repeat
  background-size cover
  margin-top 1px
.container.crumbs
  margin-top 50px
  color black
.full-width.tab{
  margin-top: 48px;
  height: 40px;
  border-bottom: #ccc 1px solid;
}
.business .row{
  margin: 0 auto;
  position: relative;
}
.row-item{
  float: left;
  height: 40px;
  width 33.3333333%;
  text-align: center;
  font-size: 18px;
}
.row-item a{
  padding: 0 4px 20px;
  margin 0 auto
  height: 40px;
  width 100px
  display: block;
  font-weight: bold;
}
.row-item a.active{
  color: #038aeb;
  border-bottom: 3px solid #038aeb;
}

.full-width.card
  margin 117px 0 200px
.business-card
  position relative
  width 280px
  height 373px
  margin-right 20px
  float left
  &:last-child
    margin-right 0
  img.bg
    position absolute
    z-index 0
    top 0
    left 0
  img.icon_off
    position absolute
    z-index 1
    top 127px
    left 100px
  img.icon_on
    position absolute
    z-index 1
    top 127px
    left 100px
    opacity 0
  &:hover img.icon_off
    opacity 0
    top 107px
    transition 0.5s
  &:hover img.icon_on
    opacity 1
    top 107px
    transition 0.5s
  .mask
    width 100%
    position absolute
    z-index 1
    height 65px
    bottom 0
    padding 16px 40px
    color white
    background-color rgba(0,0,0,0.3)
    span
      position absolute
      font-size 14px
      display block
      width 198px
      height 35px
      text-overflow ellipsis
      overflow hidden
      bottom 16px
      left 40px
  &:hover .mask
    height 100%
    transition 0.5s
    background-color rgba(0,0,0,0.5)
    z-index 0
  &:hover .mask>span
    bottom 64px
    height 60px
    text-align center
  .blue-card
    position: absolute
    top:-28px
    left:46px
    span
      position absolute
      width 188px
      height 56px
      line-height 56px
      text-align center
      z-index 5
      font-size 16px
      font-weight bold
      color white
    img
      position absolute
      z-index 1
.container.advantage
  padding 129px 10px 170px
.adv-l
  width 980px
  height 243px
  box-shadow 0px 8px 8px 0px rgba(170, 172, 169, 0.48)
  position relative
  margin 0 auto 30px
  border-radius 10px
  background-color white
  img.ico
    position absolute
    top 20px
    left 138px
  img.big
    position absolute
    top 20px
    right 160px
  div.adv-inf
    position absolute
    width 180px
    top 30px
    left 227px
    .tile
      display block
      font-size 24px
      margin-bottom 29px
    .adv
      display inline-block
      height 39px
      line-height 39px
      font-size 16px
      width 180px
      text-align center
      border solid 1px #038aeb
      margin-bottom 10px
      border-radius 19px
.adv-l.reverse
