  color white
  div.adv
    border solid 1px white
.container.join
  padding 89px 10px 181px
  &:after
    display block
    content ''
    clear both
  .join-bg
    float left
    width 560px
  form.join-form
    float right
    width 560px
    tr
      display block
      margin-bottom 10px
      td.tile
        width 98px
        font-size 16px
        color #333
      input[type="text"]
        width 430px
        height 40px
        padding 14px 21px
        font-size 14px
        border-radius 6px
        outline none
        border 1px solid #ccc
      textarea
        width 430px
        height 100px
        resize none
        padding 14px 21px
        font-size 14px
        border-radius 6px
        outline none
        border 1px solid #ccc
      a.btn
        width 120px
        height 40px
        line-height 40px
        border 1px solid #038aeb
        display block
        text-align center
        color #038aeb
        border-radius 6px
        margin-top 16px
        &:hover
          background-color #038aeb
          color white
    .address-select
      width 430px
      margin-bottom 10px
      select
        width 130px
        margin-right 15px
        height 40px
        padding 10px
        font-size 13px
        outline none
        border 1px solid #ccc
        border-radius 6px
        &:last-child
          margin-right 0
</style>



