/**
 * @file layout module
 * @author ryu
 *
 */

export const LAYOUT = 'LAYOUT';

export const state = () => {
  return {
    /**
     * 控制主框架显示
     * @type object
     */
      top:true,
      nav:true,
      foot:true
  }
};
export const mutations = {
  [LAYOUT](state, layout) {
    state = Object.assign(state, layout)
  }
};

export const actions = {
  /**
   * 记录城市选择
   *
   * @param {Function} commit commit
   * @param {Object}  city
   */
  setLayout({commit}, layout) {
    commit(LAYOUT, layout);
  }
};



