<template>
  <div id="service">
      <div class="tab">
        <ul>
          <li :class="{active:name === 'process'}"><a @click="emit('process')">查看服务进程</a></li>
          <li :class="{active:name === 'report'}"><a @click="emit('report')">购买报告</a></li>
        </ul>
      </div>
    <div class="demand-main">
      <div class="demand-item" v-for="i in 2">
        <div class="item-tile">
          <span class="time">提交时间：2018-07-07  15:43:23</span>
          <span class="nums">订单号：12365413216565</span>
          <span class="status">状态</span>
        </div>
        <div class="item-cont">
          <span class="title">需求标题</span>
          <span class="city">价格</span>
          <span class="cont">数量</span>
          <span class="company">对接的服务商</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
    export default {
        name: "service",
        props:{
          name
        },
        methods:{
          emit(a){
            this.bus.$emit('tab',a)
          }
        }
    }
</script>
<style scoped lang="stylus">
#service
  position relative
  width 880px
  height 670px
  border-radius 6px
  background-color white
  border 1px solid white
  .tab
    height 80px
    line-height 80px
    ul
      height 80px
      box-sizing border-box
      border-bottom 1px solid #ccc
      li
        height 80px
        box-sizing border-box
        display inline-block
        margin-left 50px
        font-size 16px
        color #555
        &.active
          border-bottom 2px solid #333
        a
          display block
  .demand-main
    padding 50px 11px
    .demand-item
      width 100%
      height 100px
      margin-bottom 5px
      border 1px solid #ccc
      .item-tile
        height 40px
        line-height 40px
        padding-left 20px
        font-size 12px
        padding-right 20px
        background-color #eaf8ff
        .time
          color #555
          margin-right 30px
        .status
          float right
          color #555
      .item-cont
        height 60px
        line-height 60px
        padding 0 20px
        font-size 16px
        color #333
        .title
          margin-right 10px
          display inline-block
          width 200px
        .city
          margin-right 10px
          display inline-block
          width 100px
        .cont
          margin-right 10px
          display inline-block
          width 100px
        .company
          display inline-block
          width 350px
</style>



