<template>
  <div class="full-width demand">
    <div class="container">
      <div class="table">
        <div class="table-box table-select">
          <div class="tr">您的选择:</div>
          <div class="td row">
            <div class="td-select" v-for="(i,index) in selected" @click="del(index)" :key="index">
              {{i.name}}
            </div>
          </div>
        </div>
        <div class="table-box table-service">
          <div class="tr">选择服务:</div>
          <div class="td row">
            <div class="" v-for="i in Category" style="margin-bottom: 25px">
              <div class="td-tile">{{i.name}}</div>
              <div class="td-box">
                <div class="td-item" :class="{select:j}" v-for="j in i.children" @click="selec(j)">{{j.name}}</div>
              </div>
            </div>
          </div>
        </div>
        <div class="table-box table-desc">
          <div class="tr">需求描述：</div>
          <div class="td row">
            <textarea name="describe"  cols="30" rows="10"></textarea>
          </div>
        </div>
        <div class="table-box msg">
          <div class="tr">电话号码：</div>
          <div class="td row">
            <input name="phone" class="phone2" placeholder="请输入手机号码" cols="30" rows="10">
          </div>
        </div>
        <div class="table-box code">
          <div class="tr">验证码：</div>
          <div class="td row">
            <input name="code" id="code" placeholder="请输入手机验证码" cols="30" rows="10">
            <label id="codelabel" for="code" autocomplete="none">发送验证码</label>
          </div>
        </div>
        <a href="" class="btn">提交需求</a>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "demand",
        data(){
          return{
            selected:[],
            Category:[]
          }
        },
      created(){
        let data = {
          top:true,
          nav:true,
          foot:true
        };
        this.$store.dispatch('layout/setLayout', data);
          this.getCategory()
      },
      methods:{
        getCategory(){
          this.http('api/index/getCategory').then(res=>{
            if (res.data.status===200){
              this.Category = res.data.data
            }
          })
        },
        selec(i){
            this.selected.push(i);
        },
        del(key){
          this.selected.splice(key,1)
        }
      }

    }
</script>

<style scoped lang="stylus">
  .full-width.demand
    margin-top 1px
    background-color #f5f5f5
    padding 50px 0 200px 0
    .table
      position relative
      width 1020px
      margin 0 auto
      background-color white
      border-radius 10px
      padding 50px
      .table-box
        position relative
        margin-bottom 20px
        &:after
          display block
          clear both
          content ''
        .tr
          float left
          font-size 16px
          font-weight bold
          line-height 24px
          width 80px
          margin-right 30px
        .td.row
          float left
          position relative
          width 810px
          line-height 24px
          :after
            display block
            clear both
            content ''
          .td-select
            width 15%
            height 20px
            float left
            color #038aeb
            margin-bottom 20px
            cursor pointer
            &:hover
              text-shadow #038aeb 0 0 2px
              transition 0.5s
          .td-tile
            width 60px
            font-weight bold
            float left
            margin-right 25px
          .td-box
              float left
              width 700px
              :after
                display block
                clear both
                content ''
              .td-item
                width 16%
                float left
                margin-bottom 20px
                cursor pointer
          textarea
            width 700px
            height 150px
            resize none
            padding 10px
          input
            width 270px
            height 37px
            border none
            border-bottom 1px solid #ccc
            outline none
            vertical-align top
            line-height 37px
            padding 0 0 10px 20px
          #codelabel
            position absolute
            left 180px
            cursor pointer
            &:before
              display inline-block
              content ""
              width 1px
              height 16px
              background-color #ccc
              margin-right 10px
              transform translateY(3px)
      a.btn
        width 160px
        height 40px
        line-height 40px
        border 1px solid #038aeb
        border-radius 6px
        display block
        text-align center
        color #038aeb
        font-size 18px
        margin 50px auto 0
        &:hover
          background-color #038aeb
          color #ffffff
</style>




