<template>
    <div id="city" class="top" v-if="top">
      <div class="container">
        <a><span>欢迎来到增值通服务平台</span></a>
        <div style="float: right;" class="selec">
          <a @mouseover="show = true" class="selectCity" :class="{hover:show}"><span>{{town}}</span><span style="transform: rotate(90deg);
  display: inline-block;
  margin-left: 10px;">></span></a>
          <div v-if="show" @mouseleave="show=false" class="city">
            <h5>热门城市：</h5>
            <table border="0" cellspacing="0" style="margin-top: 16px;
  margin-bottom: 26px;">
              <tr style="width: 350px;display: block;">
                <td style="width: 50px" v-for="i in hot "><a @click="go(i)">{{i.name}}</a></td>
              </tr>
            </table>
            <h5>全部城市：</h5>
            <table class="search" border="0" cellspacing="0" style="margin-top: 16px;
 ">
              <tr>
                <td>
                  <div class="">
                    <input autocomplete="off" v-model="cityInput" id="search" type="text" placeholder="输入城市名称（例如：重庆/cq）">
                  </div>
                </td>
              </tr>
              <tr>
                <td>
                  <ul class="label" v-if="result.length>0">
                    <li v-for="i in result" @click="go(i)"><span class="name">{{i.name}}</span><span class="pinyin">{{i.py}}</span></li>
                  </ul>
                </td>
              </tr>
            </table>
          </div>
          <router-link to="/login"><span>登陆/注册</span></router-link>
        </div>
      </div>
    </div>
</template>

<script type="text/ecmascript-6">
  import {mapState} from 'vuex'
    export default {
        name: "top",
      data(){
          return{
            top_show:true,
            cityInput:'',
            show:false,
            hot:[],
            result:[],
          }
      },
      created:function(){
        var _self = this;
        this.$watch('cityInput', _self.debounce(function(newSearchText){
          _self.getAnswer(newSearchText)
        }, 800));
        this.hotCity()
      },
      methods:{
        go(i){
          this.city = i.name;
          this.id = i.id;
          let path = this.$route.path;
          this.show = false;
          let city = {
            name:i.name,
            id:i.id
          }
          this.storage.addStorage('city',city);
          this.$store.dispatch('main/setMain', city);
          this.$router.push({path:path,query:{id:i.id}});
          window.location.reload()
        },
        hotCity:function(){
          this.http('api/index/hotCity').then(res=>{
            this.hot = res.data.data
          })
        },
        getAnswer:function () {
          let data = {short_name:this.cityInput};
          this.http('api/index/searchCity',data).then(res=>{
            this.result = res.data.data
          })
        },
        debounce:function(func, delay) {
          var timer;
          return function (...args) {
            if (timer) {
              clearTimeout(timer)
            }
            timer = setTimeout(function() {
              func()
            }, delay)
          }
        }
      },
      computed:{
      //   ...mapState('main',{
      //     selectedCity:state => state.selectedCity
      //   })
        ...mapState('layout',{
          top:state=>state.top
        }),
        town(){
          let city = this.storage.getStorage('city');
          return city.name
        }
      }
    }
</script>

<style scoped>

</style>



