import Vue from 'vue'
import Router from 'vue-router'
import index from '@/page/index'
import services from '@/page/services'
import demand from '@/page/demand'
import about from '@/page/about'
import detail from '@/page/details'
import introduce from '@/page/introduce'
import business from '@/page/business'
import login  from '@/page/login'
import sign  from '@/page/signup'
import user from '@/page/usercenter'
Vue.use(Router)

export default new Router({
  mode: 'history',
  scrollBehavior(to, from, savedPosition){
    return { x: 0, y: 0 }
  },
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path:'/services',
      name:'services',
      component:services
    },{
      path:'/demand',
      name:'demand',
      component:demand
    },{
      path:'/about',
      name:'about',
      component:about
    },{
      path:'/details/:id',
      name:'detail',
      component:detail
    },{
      path:'/introduce/:id',
      name:'introduce',
      component:introduce
    },{
      path:'/business',
      name:'business',
      component:business
    },{
      path:'/login',
      name:'login',
      component:login
    },{
      path:'/sign',
      name:'sign',
      component:sign
    },
    {
      path:'/user',
      name:'user',
      component:user
    }
  ]
})




