<template>
  <div>
    <div class="container main">
      <div class="mainl">
        <div class="classify">
          <div class="tr">
            <div class="td-title">
              服务类别:
            </div>
            <div class="td">
              <div class="td-item"><a @click=" businessService({parent_id:0,child_id:0})" :class="{select:options.parent_id===0}">不限</a></div>
              <div class="td-item" v-for="i in business.fatherCate"><a :class="{select:i.id===options.parent_id}" @click=" businessService({parent_id:i.id,child_id:0
})">{{i.name}}</a></div>
            </div>
          </div>
          <div class="tr">
            <div class="td-title">
              服务项目:
            </div>
            <div class="td">
              <div class="td-item"><a @click=" businessService({child_id:0})" :class="{select:options.child_id===0}">不限</a></div>
              <div class="td-item" v-for="i in business.childCate"><a @click=" businessService({child_id:i.id})" :class="{select:options.child_id===i.id}">{{i.name}}</a></div>
            </div>
          </div>
          <div class="tr">
            <div class="td-title">
              排序方式:
            </div>
            <div class="td">
              <div class="td-item" v-for="i in 2"><a :class="{select:options.type===i}" @click="businessService({type:i})">{{i | type}}</a></div>
            </div>
          </div>
        </div>
        <div class="service">
          <service-item v-for="(i,n) in business.serviceData" :key="n" :class="{margin0:(n+1)%3===0}" :indent="i" :num="n"></service-item>
        </div>
        <div class="page" v-if="business.countPage>1">
          <ul class="page-control">
            <li class="page-item page-up" v-if="business.page>1" @click="businessService({page:parseInt(business.page)-1})">上一页</li>
            <li class="page-item" v-for="i in 5" :class="[{'more-page':i===4},{'active':i===options.page}]">
              <span v-if="i!=4">{{i}}</span>
              <span v-else>···</span>
            </li>
            <li class="page-item page-down" @click="businessService({page:parseInt(business.page)+1})">下一页</li>
          </ul>
          <div class="page-jump">
            <span>到第</span>
            <input type="text">
            <span>页</span>
          </div>
        </div>
      </div>
      <div class="mainr">
        <p style="margin-bottom: 20px;font-weight: bold;font-size: 16px;">优秀企业推荐</p>
        <div class="good-company" v-for="i in recommend">
          <div class="company-img">
            <img src="" alt="">
          </div>
          <div class="company-detail" >
            <p class="company-title" v-html="i.name"></p>
            <p class="sm-font company-add">
              <span class="add-province">省份：{{i.city_name}}</span>
              <span class="add-city">城市：{{i.child_city_name}}</span>
            </p>
            <p class="sm-font company-sale">
              <span class="company-count">销量：{{i.total_num}}</span>
              <span class="company-odds">好评率：{{i.evaluation}}%</span>
            </p>
            <p class="sm-font company-operation">
              擅长领域：{{i.advantage}}
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import ServiceItem from '@/components/service-item'
    export default {
      name: "services",
      components:{
        ServiceItem
      },
      created(){
        let data = {
          top:true,
          nav:true,
          foot:true
        };
        this.$store.dispatch('layout/setLayout', data);
        this.recommendService();
        this.businessService()
      },
      filters:{
        type (i){
          if (i===1){
            return '成交量'
          } else if(i===2){
            return '好评率'
          }
        }
      },
      data(){
          return{
            recommend:[],//热门推荐列表
            business:'',//分类列表推荐
            //请求携带参数
            options:{
              page:1,
              parent_id:0,
              child_id:0,
              type:1,
              order:'asc'
            }
          }
      },
      methods:{
        recommendService(){
          this.http('api/service/recommendService').then(res=>{
            this.recommend = res.data.data
          })
        },
        businessService(opt){
          let data = this.options;
          if (opt){
            data = Object.assign(data,opt)
          }
          this.http('api/service/businessService',data).then(res=>{
            if (res.data.status === 200){
              this.business =  res.data.data;
            }
          });
          this.options = data
        }
      }
    }
</script>

<style scoped>
  .container.main{
    margin-top: 50px;
  }
  div.service{
    margin-bottom: 20px;
    margin-top: 60px;
    min-height: 500px;
  }
  div.service:after{
    display: block;
    content: '';
    clear: both;
  }
  div.main:after{
    display: block;
    content: '';
    clear: both;
  }
  div.mainl{
    width: 880px;
    float: left;
    margin-right: 20px;
  }
  div.mainr{
    width: 280px;
    float: left;
  }
  /*服务分类*/
  div.tr{
    width: 880px;
    margin-bottom: 30px;
    border-radius: 6px;
    display: block;
    background-color: #f2f2f2;
  }
  div.tr:after{
    display: block;
    content: '';
    clear: both;
  }
  div.td-title{
    width: 120px;
    height: 60px;
    font-size: 16px;
    font-weight: bold;
    text-indent: 20px;
    line-height: 60px;
    float: left;
  }
  div.td{
    width:760px;
    float: left;
  }
  div.td:after{
    clear: both;
    display: block;
    content: '';
  }
  .td-item{
    min-width:86px;
    max-width:95px;
    height: 40px;
    line-height: 40px;
    margin-top: 10px;
    float: left;
  }
  /*服务列表*/

  /*分页*/
  .page{
    min-width: 502px;
    height: 40px;
    line-height: 40px;
    margin-bottom: 180px;
    text-align: center;
    font-weight: bold;
    margin-top: 60px;
  }
  ul.page-control{
    display: inline-block;
  }
  ul.page-control li{
    display: inline-block;
  }
  li.page-item{
    border-radius: 6px;
    border:solid 1px #cccccc;
    margin-right: 10px;
    width: 40px;
    height: 40px;
    cursor: pointer;
  }
  li.page-item.more-page{
    border: none;
  }
  li.page-item.active,li.page-item:hover{
    border: none;
    color: white;
    background-color: #038aeb;
  }
  li.page-up,li.page-down{
    width: 60px;
  }
  .page-jump{
    display: inline-block;
  }
  .page-jump input{
    width: 40px;
    height: 40px;
    border-radius: 6px;
    border:solid 1px #cccccc;
    outline: none;
    margin: 0 6px;
    text-align: center;
  }
  /*企业推荐*/
  .good-company{
    width: 280px;
    height: 120px;
    margin-bottom: 20px;
    background-color: #f2f2f2;
    border-radius: 6px;
    padding: 20px 10px;
  }
  .company-img{
    float: left;
    width: 80px;
    height: 80px;
    background-color: black;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    border-radius: 2px;
  }
  .company-img img{
    width: 100%;
    height: 100%;
    display: block;
  }
  .company-detail {
    float: left;
    max-width: 170px;
    height: 80px;
    margin-left: 10px;
    transform: translateY(-5px);
  }
  .company-detail p{
    width: 170px;
    margin-bottom: 9px;
  }
  .company-title{
    font-size: 16px;
    font-weight: bold;
  }
  .company-odds,.add-city{
    float: right;
  }
  a.select{
    color: #038aeb;
  }
</style>



