<template>
    <div class="signup">
      <div class="whitespace">
        <div class="container">
          <img src="" alt="logo">
        </div>
      </div>
      <div class="full-width login-main" :style="{backgroundImage:`url(${img})`}">
        <div class="container">
          <div class="sign-box">
            <p class="title">注册增值通</p>
            <form class="sign-form">
              <input type="text" v-model="name" placeholder="用户名">
              <input type="text" v-model="phone" placeholder="请输入手机号">
              <label for="code" class="i"></label>
              <label class="code" for="code" @click="sendMss()" v-if="btn">{{getCode}}</label>
              <label class="code" for="code" v-if="!btn">{{getCode}}</label>
              <input id="code" v-model="code" type="text" placeholder="请输入验证码">
              <input type="text" v-model="password" placeholder="请输入密码">
              <div class="agreement">
                <input id="check" v-model="agree" type="checkbox" style="display: none">
                <label class="check" :class="{checked:agree}" for="check"></label>
                我已阅读并接受《用户协议》
              </div>
              <a class="sign-in-btn" @click="register">注册</a>
              <p style="font-size: 12px;text-align: right;margin-top: 5px"><router-link to="/login">已有账号，立即登录</router-link></p>
            </form>
          </div>
        </div>
      </div>
      <div class="login-foot">
        <div class="container">
          <div class="login-footer">
            <p style="margin-bottom: 46px">关于我们  |  联系我们  |  帮助中心  |  购物流程  |  会员中心  |  合约平台  |  优惠券  |  办公资讯  |  网站地图  |  手机商城</p>
            <p>版权所有 © CopyRight 2007-2018 欧菲斯办公伙伴控股有限公司 渝ICP备14006025号-1</p>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
      name: "signup",
      data(){
        return{
          img:'static/login/login-bg.png',
          agree:true,
          getCode:'获取验证码',
          btn:true,
          name:'',
          phone:'',
          code:'',
          password:''
        }
      },
      methods:{
        sendMss(){
          let time = 60;
          this.btn = false;
          let _self = this;
          _self.getCode = `请等待${time}秒`;
          let timer = setInterval(function () {
            time -=1;
            _self.getCode = `请等待${time}秒`;
            if (time === 0){
              _self.btn = true;
              _self.getCode = '获取验证码';
              clearInterval(timer)
            }
          },1000);
          this.http('api/verify/sendSms',{phone:this.phone}).then(
            res=>{
              if (res.data.status === 200){

              }else{
                clearInterval(timer)
              }
            }
          )
        },
        register(){
          let data= {
            username:this.name,
            mobile:this.phone,
            password:this.password,
            verify_code:this.code
          };
          this.http('api/member/register',data).then(res=>{

          })
        }
      },
      created(){
        let data = {
          top:false,
          nav:false,
          foot:false
        };
        this.$store.dispatch('layout/setLayout', data);
      }
    }
</script>

<style scoped lang="stylus">
  .signup
    min-height 932px
  .whitespace
    position: relative
    height 60px
  .login-main
    height 640px
    background center no-repeat
    background-size cover
  .login-foot
    margin 90px auto 0
    text-align center
    p
      color #333
  .sign-box
    width 350px
    height 500px
    position absolute
    background-color white
    top 75px
    right 10px
    padding 30px
    .title
      font-size 24px
      font-weight bold
    .sign-form
      width 100%
      position relative
      padding 0 20px
      input
        width 100%
        height 60px
        line-height 47px
        margin-top 6px
        vertical-align top
        border-bottom 1px solid #ccc
        border-top none
        border-right none
        border-left none
        outline none
      label.code
        color #039dff
        cursor pointer
        position absolute
        right 20px
        top 156px
      label.i
        display block
        width 1px
        height 16px
        background-color #ccc
        position absolute
        right 105px
        top 157px
      .agreement
        margin-top 14px
        font-size 12px
        label.check
          width 12px
          height 12px
          vertical-align -1px
          margin-right 6px
          display inline-block
          border: solid 1px #cccccc;
          cursor pointer
        label.checked
          background-color #29524d
  .sign-in-btn
    display block
    width: 250px;
    height: 50px;
    line-height 50px;
    text-align center;
    font-size 18px;
    margin-top 45px
    color white
    background-image: linear-gradient(180deg, #77c1fa 0%, #038aeb 100%), linear-gradient(#333333, #333333);
    background-blend-mode: normal, normal;
    box-shadow: 0px 4px 5px 0px rgba(12, 142, 236, 0.48);
</style>



