<template>
  <div class="service-item">
    <div class="img-box">
      <a @click="$router.push(`/details/${indent.id}`)">
        <img  src="" alt="">
      </a>
    </div>
    <div class="detail-box">
      <p class="detail-jiage">
        <span >￥{{indent.end_price}}</span><em v-show="indent.is_standard===1" style="font-size:12px;color: #fe7405;">起</em>
        <em style="font-size: 12px;float: right;display: inline-block">咨询量：50</em>
      </p>
      <p class="detail-title"><a href="">{{indent.name}}</a></p>
      <p class="detail-company">
        <router-link :to="{name:'introduce',params:{id:indent.business_id}}" style="text-overflow: ellipsis;overflow: hidden;white-space: nowrap;width: 190px" v-html="indent.business_name"></router-link>
      </p>
      <a class="btn" v-if="indent.is_standard===1" @click="$router.push('/demand')">立即购买</a>
      <a class="btn" v-else @click="$router.push('/demand')">提交需求</a>
    </div>
  </div>
</template>

<script>

    export default {
        name: "service-item",
        props:[
          'indent',
          'num'
        ],
        data(){
          return{
            city:''
          }
        }
    }
</script>

<style scoped>
  .service-item{
    width: 280px;
    height: 467px;
    display: block;
    float: left;
    margin-right: 20px;
    border-radius: 4px;
    background-color: #f2f2f2;
    overflow: hidden;
    margin-bottom: 20px;
  }
  .service-item.margin0{
    margin-right: 0;
  }
  .img-box{
    position: relative;
    width: 280px;
    height: 280px;
    overflow: hidden;
    background-color: black;
  }
  .img-box img{
    width: 100%;
    height: 100%;
    display: block;
  }
  .detail-jiage{
    padding: 18px 20px;
    line-height:20px;
  }
  .detail-jiage span{
    font-size: 24px;
    height: 20px;
    font-weight: bold;
    line-height: 25px;
    color: #fe7405;
  }
  .detail-title{
    padding: 0 20px;
    margin-bottom: 8px;
    height: 20px;
    font-size: 16px;
    font-weight: bold;
    width: 280px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    word-wrap: normal;
  }
  .detail-company{
    padding: 0 20px;
    margin-bottom: 20px;
    position: relative;
  }
  a.btn{
    width: 120px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    display: block;
    margin: 0 auto;
    border-radius: 6px;
    border: solid 1px #038aeb;
    color: #038aeb;
  }
  a.btn:hover,a.btn.active{
    color: white;
    background-color: #038aeb;
    transition: 0.3s;
  }
</style>



