<template>
  <div class="detail">
    <div class="full-width details-banner" :style="{backgroundImage:`url(${img})`}">
      <div class="container">
        <div class="info">
          <p class="info-title">{{content.name}}</p>
          <p class="info-co">{{content.service_desc}}</p>
          <p class="info-n">*暂不接待单次服务订单</p>
          <a class="btn">提交需求</a>
        </div>
      </div>
    </div>
    <div class="container crumbs">
      您当前的位置:您当前的位置 ：全部服务  >>  {{content.name}}   >>  {{tab[tabActive]}}
    </div>
    <div class="full-width tab">
      <div class="container">
        <div class="row" style="width: 780px">
          <div class="row-item" v-for="(i,n) in tab" >
            <a @click="tabActive = n" :class="{active:tabActive===n}">{{i}}</a>
          </div>
        </div>
      </div>
    </div>
    <div class="full-width">
      <detail-case :html="html[3]" v-if="tabActive === 3"></detail-case>
      <detail-main :html="html" :active="tabActive" v-else ></detail-main>
    </div>
  </div>
</template>

<script>
  import DetailMain from '@/components/detailmain'
  import DetailCase from '@/components/detailcase'
    export default {
        name: "detail",
      components:{
          DetailMain,
          DetailCase
      },
      created(){
        let data = {
          top:true,
          nav:true,
          foot:true
        };
        this.$store.dispatch('layout/setLayout', data);
        this.getDetail()
      },
      methods:{
          getDetail(){
            let service_id ={service_id:this.$route.params.id};
            this.http('api/service/businessServiceDetail',service_id).then(res=>{
              if (res.data.status === 200){
                this.content = res.data.data;
                this.html.push(res.data.data.info);//服务内容
                this.html.push(res.data.data.process);//服务流程
                this.html.push(res.data.data.company_desc);//公司介绍
                this.html.push(res.data.data.case_desc);//合作案例
                this.html.push(res.data.data.service_area);//服务区域
              }else{
                this.$router.push('/services')
              }
            })
          }
      },
        data(){
          return{
            content:{},
            html:[],
            tabActive:0,
            img:'/static/detail/3.png',
            tab:['服务内容','服务流程','公司介绍','合作案例','服务区域'],

          }
        }
    }
</script>

<style scoped>
  .full-width.details-banner{
    height: 600px;
    background: center no-repeat;
    background-size: cover;
    margin-top: 1px;
  }
  .full-width.details-banner .container{
    height: 600px;
  }
  .info{
    position: absolute;
    width: 460px;
    height: 340px;
    background-color: rgba(0,0,0,0.3);
    margin-top:127px;
    border-radius: 6px;
    padding: 47px 50px;
  }
  .info-title{
    font-size: 30px;
    color: white;
    font-weight: bold;
    margin-bottom:27px ;
    overflow: hidden;
    text-overflow-ellipsis: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .info-co{
    font-size: 16px;
    line-height: 24px;
    margin-bottom:27px ;
    color: white;
  }
  .info-n{
    color: white;
    margin-bottom: 43px;
  }
  a.btn{
    width: 120px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    display: block;
    border-radius: 6px;
    border: solid 1px #038aeb;
    color: white;
    background-color: #038aeb;
  }
  a.btn:hover,a.btn.active{
    color: white;
    background-color: #039dff;
    transition: 0.3s;
  }
  .container.crumbs{
    margin-top: 50px;
  }
  .full-width.tab{
    margin-top: 48px;
    height: 40px;
    border-bottom: #ccc 1px solid;
  }
  .detail .row{
    margin: 0 auto;
    position: relative;
  }
  .row-item{
    float: left;
    height: 40px;
    width: 20%;
    text-align: center;
    font-size: 18px;
  }
  .row-item a{
    width: 80px;
    padding: 0 4px 20px;
    height: 40px;
    display: block;
    font-weight: bold;
  }
  .row-item a.active{
    color: #038aeb;
    border-bottom: 3px solid #038aeb;
  }

</style>



