/**
 * @file main module
 * @author ryu
 *
 */

export const MAIN = 'MAIN';

export const state = () => {
  return {
    /**
     * @type object
     */
    selectedCity:{
      name:'全国',
      id:'0'
    }
  };
};
export const mutations = {
  [MAIN](state, main) {
    state = Object.assign(state, main)
  }
};

export const actions = {
  /**
   * 记录城市选择
   *
   * @param {Function} commit commit
   * @param {Object}  city
   */
  setMain({commit}, main) {
    commit(MAIN, main);
  }
};



