<template>
    <div class="introduce">
      <div class="full-width intro_banner" :style="{backgroundImage:`url(${banner})`}">

      </div>
      <div class="container crumbs">
        您当前的位置:您当前的位置 ：服务商介绍  >>
      </div>
      <div class="container company-introduce">
        <h1>
          公司介绍
        </h1>
        <div class="row introduce-main">

        </div>
      </div>
      <div class="container service-introduce">
        <h1>
          服务项目
        </h1>
        <div class="row">
          <service-item v-for="(i,n) in 8 " :class="{margin0:(n+1)%4===0}" :key="n" :indent="i" :num="n"></service-item>
        </div>
      </div>
    </div>
</template>

<script>
  import ServiceItem from '@/components/service-item'
    export default {
        name: "introduce",
        components:{
          ServiceItem
        },
      created(){
        let data = {
          top:true,
          nav:true,
          foot:true
        };
        this.$store.dispatch('layout/setLayout', data);
      },
        data(){
          return{
            banner:'/static/index/pic/banner3.jpg'
          }
        }
    }
</script>

<style scoped lang="stylus">
  .full-width.intro_banner
    height: 600px;
    background: center no-repeat;
    background-size: cover;
    margin-top: 1px;
  .container.crumbs
    margin-top: 50px;
  .container.company-introduce h1
    font-size: 30px;
    font-weight bold;
    margin-top 45px
  .container.company-introduce h1:before
    display: inline-block;
    width: 2px;
    background-color: #0394f6;
    height: 30px;
    content: '';
    transform: translateY(5px);
    margin-right: 12px;
  .row.introduce-main
    padding 80px 0 150px
  .container.service-introduce
    h1
      font-size: 30px;
      font-weight bold;
      margin-top 45px
      &:before
        display: inline-block;
        width: 2px;
        background-color: #0394f6;
        height: 30px;
        content: '';
        transform: translateY(5px);
        margin-right: 12px;
    .row
      padding 80px 0 150px
    &:after
      display block
      content ''
      clear both
</style>



