<template>
    <div id="account">
      <div class="tab">
        <ul>
          <li :class="{active:name === 'info'}"><a @click="emit('info')">基本信息</a></li>
          <li :class="{active:name === 'safe'}"><a @click="emit('safe')">账户安全</a></li>
          <li :class="{active:name === 'password'}"><a @click="emit('password')">修改密码</a></li>
        </ul>
      </div>
      <div class="main-content">
        <div class="info" v-if="name === 'info'">
          <form
          >
            <table cellspacing="0" cellpadding="0" width="600">
              <tr v-for="(i,n) in info">
                <td class="taile">{{i.name}}</td>
                <td class="input">
                  <div class="avatar" v-if="n === 0">
                    <img src="" alt="">
                    <a class="btn">修改头像</a>
                  </div>
                  <input type="text" :placeholder="i.placeholder" v-if="n!==0">
                </td>
              </tr>
            </table>
          </form>
          <div class="commit">
            <a  class="btn">确定</a>
          </div>
        </div>
        <div class="safe" v-if="name === 'safe'">
          <div class="safe-form">
            <div class="tr" v-for="(i,n) in safe">
              <div class="td-tile">{{i.item}}</div>
              <div class="td-cont">
                <p>{{i.cont}}</p>
                  <a v-for="j in i.action" class="action" :class="{unact:j.type === 1}" >{{j.type | act}}</a>
              </div>
            </div>
          </div>
        </div>
        <div class="password" v-if="name==='password'">
          <table cellspacing="0" cellpadding="0" width="600">
            <tr v-for="i in password">
              <td class="tile">{{i.name}}</td>
              <td class="input">
                <input type="text" :placeholder="i.placeholder">
              </td>
            </tr>
            <tr>
              <td class="tile"></td>
              <td class="input">
                <a class="btn">确认</a>
              </td>
            </tr>
          </table>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "account",
        props:{name},
        methods:{
          emit(a){
            this.bus.$emit('tab',a)
          }
        },
      filters:{
          act(value){
            switch (value){
              case 0:
                return '修改'
              case 1:
                return '解绑'
              case 2:
                return '绑定'
            }
          }
      },
        data(){
          return{
            info:[
              {name:'当前头像：',placeholder:''},
              {name:'账户名：',placeholder:'如歌浅燕'},
              {name:'企业名称：',placeholder:'请输入您的公司名称'},
              {name:'企业名称：',placeholder:'请输入您公司的地址'},
              {name:'联系人：',placeholder:'请输入您的姓名'},
              {name:'职位：',placeholder:'请输入您的职位'},
              {name:'电话：',placeholder:'请输入您的电话号码'},
              {name:'邮箱：',placeholder:'请输入您的邮箱'},
            ],
            safe:[
              //action type
              //,0 修改 ,1 解绑,2 绑定

              {item:'账户名：',cont:'如歌浅燕',action:[
                  {type:0,act:''}
                ]},
              {item:'验证手机：',cont:'18523840771',action:[
                  {type:0,act:''},
                  {type:1,act:''}
                ]},
              {item:'验证邮箱：',cont:'18523840771@163.com',action:[
                  {type:0,act:''},
                  {type:1,act:''}
                ]},
              {item:'关联微信：',cont:'您未关联微信，关联后可以直接用微信登录',action:[
                  {type:2,act:''}
                ]}
            ],
            password:[
              {name:'原始密码：',placeholder:'请输入原始密码'},
              {name:'新密码：',placeholder:'请输入6-20位的数字、英文、字符'},
              {name:'确认密码：',placeholder:'请再次确认密码'}
            ]
          }
        }
    }
</script>

<style scoped lang="stylus">
 @require "~@/style/account.styl"
</style>



