<template>
    <div class="user">
      <div class="container user-center">
        <div class="sidebarL">
          <div class="child info">
            <div class="avatar">
              <img src="" alt="avatar">
            </div>
            <p>账户名</p>
          </div>
          <div class="child demand">
            <h1 class="tile">我提交的需求</h1>
            <p class="item"><a @click="jump('demand','todo')">已提交的需求</a></p>
            <p class="item"><a @click="jump('demand','doing')">服务中的需求</a></p>
            <p class="item"><a @click="jump('demand','done')">已完成的需求</a></p>
          </div>
          <div class="child service">
            <h1 class="tile">我购买的服务</h1>
            <p class="item"><a @click="jump('service','process')">查看服务进程</a></p>
            <p class="item"><a @click="jump('service','report')">服务报告</a></p>

          </div>
          <div class="child account ">
            <h1 class="tile">资料与账号</h1>
            <p class="item"><a @click="jump('account','info')">基本信息</a></p>
            <p class="item"><a @click="jump('account','safe')">账户安全</a></p>
            <p class="item"><a @click="jump('account','password')">修改密码</a></p>
          </div>
        </div>
        <div class="sidebarR">

            <account v-if="part === 'account'"
              :name="tab"
            ></account>
            <demand v-if="part ==='demand'"
              :name="tab"
            ></demand>
            <service v-if="part === 'service'"
              :name="tab"
            ></service>
        </div>
      </div>
    </div>
</template>

<script>
  import account from '@/components/user/account';
  import demand from '@/components/user/demand';
  import service from '@/components/user/service';
    export default {
        name: "user",
        data(){
          return{
            part:'demand',
            tab:'todo'
          }
        },
        created(){
          let data = {
            top:true,
            nav:true,
            foot:true
          };
          this.$store.dispatch('layout/setLayout', data);
          let _self = this;
          this.bus.$on('tab',function (a) {
            _self.tab = a
          })
        },
        components:{
          account,
          demand,
          service
        },
        methods:{
          jump(a,b){
            this.part = a;
            this.tab = b
          }
        }
    }
</script>

<style scoped lang="stylus">
.user
  background-color #f2f2f2
  .container.user-center
    padding 30px 10px 200px
    &:after
      display block
      content ''
      clear both
    .sidebarL
      margin-top 0px
      position: relative
      float left
      width 280px
      min-height 670px
      margin-right 20px
      .child
        position relative
        width 280px
        margin-bottom 10px
        background-color white
        border-radius 6px
        border 1px solid white
        h1.tile
          margin-top 20px
          font-size 18px
          padding-left 20px
          color black
          font-weight bold
          border-left 2px solid #039dff
        p.item
          margin-top 12px
          padding-left 20px
          color #333
      .info
        height 210px
        p
          font-size 16px
          text-align center
          position: absolute
          width 100%
          bottom 35px
          color: #333
        .avatar
          position absolute
          width 100px
          height  100px
          top 20px
          left 90px
          border-radius 50%
          background-color #cccccc
          overflow hidden
          img
            display block
            width 100%
            height 100%
      .demand
        height 154px
      .service
        height 122px
      .account
        height 154px
    .sidebarR
      position relative
      float left
      width 880px
      min-height 670px
      height auto
</style>



