<template>
    <div class="detail-main">
      <div class="container detail-info" v-for="(i,n) in html" v-if="active === n" v-html="i">

      </div>
    </div>
</template>

<script>
    export default {
        name: "detailmain",
        props:{
          html:'',
          active:'',
        }
    }
</script>

<style scoped>
  .container.detail-info{
    margin: 130px auto 200px;
  }
</style>



