<template>
  <div id="app">
    <top></top>
    <navigation></navigation>
    <transition name="fade" mode="out-in">
      <router-view/>
    </transition>
    <foot></foot>
  </div>
</template>

<script>
import top from './layout/top'
import navigation from './layout/nav'
import foot from './layout/foot'
export default {
  name: 'App',
  components:{
    top,
    navigation,
    foot
  }
}
</script>

<style>
@import "style/layout.css";
@import "style/public.css";
</style>



