import axios from 'axios';
import qs from 'qs';
import storage from './storage'
import promise from 'es6-promise'
promise.polyfill()
import {createStore} from './store.js'
let store = createStore()
//保存城市ID。存放在js和localstorage里面
let city = 0;
if (storage.hasItem('city')){
  city = storage.getStorage('city').id
}

export default function(_url,_data,_method) {
  let url = `http://api.officezzt.com/${_url}`;
  let content = {city_id:city};
  if(_data){
     content = Object.assign(_data,content);
  }
  content = qs.stringify(content)
  if (_method === 'get' ){
      return axios.get(url,content)
  }else{
    return axios.post(url,content)
  }
}



