<template>
    <div class="login">
      <div class="whitespace">
        <div class="container">
          <img src="" alt="logo">
        </div>
      </div>
      <div class="login-main full-width" :style="{backgroundImage:`url(${img})`}">
        <div class="container">
          <div class="input-box">
            <p class="input-title">登陆增值通</p>
            <div class="input-main">
              <div class="input-tab">
                <a @click="type = 1 " :class="{active:type===1}">账号登录</a>
                <a @click="type = 2 " :class="{active:type===2}">短信验证</a>
              </div>
              <form class="input-form" v-if="type===1">
                <input type="text" class="user" :class="{shake:warnu}" v-model="phone" autocomplete="off" placeholder="用户名/手机号/邮箱">
                <input type="password" class="password" :class="{shake:warnp}" v-model="password" placeholder="请输入密码">
              </form>
              <form class="input-form" v-if="type===2">
                <input type="text" class="user" v-model="phone" autocomplete="off" placeholder="用户名/手机号/邮箱">
                <label class="i" for="code"></label>
                <label for="code" @click="sendMss()" v-if="btn">{{getCode}}</label>
                <label for="code" v-if="!btn">{{getCode}}</label>
                <input id="code" name="code" type="text" v-model="code" class="password" placeholder="请输入验证码">
              </form>
              <div class="sign-in">
                <router-link to="/sign" class="join" >账号注册</router-link>
                <a class="forget" @click="type = 2 ">忘记密码</a>
              </div>
              <a class="sign-in-btn" @click="login">登陆</a>
            </div>
          </div>
        </div>
      </div>
      <div class="login-foot">
        <div class="container">
          <div class="login-footer">
            <p style="margin-bottom: 46px">关于我们  |  联系我们  |  帮助中心  |  购物流程  |  会员中心  |  合约平台  |  优惠券  |  办公资讯  |  网站地图  |  手机商城</p>
            <p>版权所有 © CopyRight 2007-2018 欧菲斯办公伙伴控股有限公司 渝ICP备14006025号-1</p>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "login",
        data(){
          return{
            btn:true,
            img:'static/login/login-bg.png',
            type:1,
            phone:'',
            password:'',
            code:'',
            getCode:'获取验证码',
            warnu:true,
            warnp:false
          }
        },
        created(){
          let data = {
            top:false,
            nav:false,
            foot:false
          };
          this.$store.dispatch('layout/setLayout', data);
        },
        methods:{
          sendMss(){
            let time = 60;
            this.btn = false;
            let _self = this;
            _self.getCode = `请等待${time}秒`;
            let timer = setInterval(function () {
              time -=1;
              _self.getCode = `请等待${time}秒`;
              if (time === 0){
                _self.btn = true;
                _self.getCode = '获取验证码';
                clearInterval(timer)
              }
            },1000);
            this.http('api/verify/sendSms',{phone:this.phone}).then(
              res=>{
                if (res.data.status === 200){

                }else{
                  clearInterval(timer)
                }
              }
            )
          },
          login(){
            let data = {
              loginName:this.phone,
              type:this.type,
              password:this.password,
              verify_code:this.code
            };
            this.http('api/member/login',data).then(res=>{
                if (res.data.status !== 200){
                  confirm(res.data.msg)
                }
            })
          }
        }
    }
</script>

<style scoped lang="stylus">
  a.active{
    color #038aeb
  }
.login
  min-height 932px
.whitespace
  position: relative
  height 60px
.login-main
  height 640px
  background center no-repeat
  background-size cover
  .input-box
    position absolute
    width 350px
    height 450px
    background-color white
    top:125px
    right:10px
    padding 30px
    .input-title
      font-size 24px
      font-weight bold
    .input-main
      position relative
      margin  40px 20px
      width 250px
      height 284px
      .input-tab
        width 100%
        &:after
          clear both
          content ''
          display block
        a
          width 50%
          display inline-block
          float left
          text-align center
      .input-form
        position relative
        margin-top 35px
        width 100%
        label.i
          width 1px
          height 16px
          right 86px
          background-color #ccc
        label
          position: absolute
          right 0
          bottom 15px
          color #038aeb
          cursor pointer
          &:before
            display: block
            width 1px
            height 15px
            background-color #cccccc
        input
          width 100%
          height 47px
          line-height 47px
          vertical-align top
          border-bottom 1px solid #ccc
          border-top none
          border-right none
          border-left none
          outline none
        input.password
          margin-top 15px
      .sign-in
        margin-top 10px
        margin-bottom 46px
        &:after
          clear both
          content ''
          display block
        .join
          font-size 12px
          float left
        .forget
          font-size 12px
          float right
      .sign-in-btn {
        display block
        width: 250px;
        height: 50px;
        line-height 50px;
        text-align center;
        font-size 18px;
        color white
        background-image: linear-gradient(180deg, #77c1fa 0%, #038aeb 100%), linear-gradient(#333333, #333333);
        background-blend-mode: normal, normal;
        box-shadow: 0px 4px 5px 0px rgba(12, 142, 236, 0.48);
      }

.login-foot
  margin 90px auto 0
  text-align center
  p
    color #333
</style>



