              'static/index/pic/banner2.JPG',
              'static/index/pic/banner3.jpg',
              'static/index/pic/banner4.jpg',
            ]
          }
      },
      created(){
        let data = {
          top:true,
          nav:true,
          foot:true
        };
        this.$store.dispatch('layout/setLayout', data);
        this.delay();
        this.getMenu()
      },
      destroyed(){
          clearInterval(this.time)
      },
      methods:{
        link:function(url){
            let _url = url;
            let _query = this.$route.query;
            this.$router.push({path:_url,query:_query})

        },
        getMenu:function(){
          this.http('api/index/getCategory').then(
              res=>{
                if(res.data.status === 200){
                  this.items = res.data.data
                }
              }
            )
        },
        //轮播图
        delay:function(){
          let time = setInterval( ()=> {
            if (this.banner_active<this.banner.length-1){
              this.banner_active+=1
            }else{
              this.banner_active = 0
            }
          },5000)
        },
        changeBanner:function(n){
          this.banner_active = n
        }
      },
      computed:{
        banneractive:function () {
          return
        }
      },
      beforeCreate(){

      }
    }
</script>

