<template>
    <div class="detail-case">
        <div class="full-width case-banner">
            <div class="container casebox">
                <div class="case-itm" v-for="i in 4" >
                  <div class="case-img">
                    <img src="" alt="">
                  </div>
                  <p class="case-title">科大讯飞</p>
                </div>
            </div>
        </div>
        <div class="container case-b">
            <div class="case-info" v-html="html">

            </div>
            <div class="case-product">
              <div class="good-company" v-for="i in 5">
                <div class="company-img">
                  <img src="" alt="">
                </div>
                <div class="company-detail" >
                  <p class="company-title">Gox原创音乐工作室</p>
                  <p class="sm-font company-add">
                    <span class="add-province">省份：四川</span>
                    <span class="add-city">城市：成都</span>
                  </p>
                  <p class="sm-font company-sale">
                    <span class="company-count">销量：66</span>
                    <span class="company-odds">好评率：86%</span>
                  </p>
                  <p class="sm-font company-operation">
                    擅长领域：音乐制作
                  </p>
                </div>
              </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "detailcase",
        props:{
          html:''
        }
    }
</script>

<style scoped>
  .container.casebox{
    padding: 90px 10px 50px;
  }
  .case-itm{
    display: inline-block;
    width: 280px;
    height: 270px;
    margin-right: 20px;
    box-shadow: 0px 8px 8px 0px rgba(86, 84, 85, 0.35);
  }
  .case-itm:last-child{
    margin-right: 0;
  }
  .case-img{
    width: 280px;
    height: 210px;
    background-color: #038aeb;
  }
  .case-title{
    height: 60px;
    line-height: 60px;
    text-align: center;
  }

  .case-info{
    width: 880px;
    float: left;
    margin-right: 20px;
  }
  .case-product{
    width: 280px;
    float: left;
  }

  .good-company{
    width: 280px;
    height: 120px;
    margin-bottom: 20px;
    background-color: #f2f2f2;
    border-radius: 6px;
    padding: 20px 10px;
  }
  .company-img{
    float: left;
    width: 80px;
    height: 80px;
    background-color: black;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    border-radius: 2px;
  }
  .company-img img{
    width: 100%;
    height: 100%;
    display: block;
  }
  .company-detail {
    float: left;
    max-width: 170px;
    height: 80px;
    margin-left: 10px;
    transform: translateY(-5px);
  }
  .company-detail p{
    width: 170px;
    margin-bottom: 9px;
  }
  .company-title{
    font-size: 16px;
    font-weight: bold;
  }
  .company-odds,.add-city{
    float: right;
  }
  .container.case-b{
    margin-bottom: 200px;
  }
  .container.case-b:after{
    clear: both;
    display: block;
    content: '';
  }
</style>



