<template>
  <div id="demand">
    <div class="tab">
      <ul>
        <li :class="{active:name === 'todo'}"><a @click="emit('todo')">已提交的需求</a></li>
        <li :class="{active:name === 'doing'}"><a @click="emit('doing')">服务中的需求</a></li>
        <li :class="{active:name === 'done'}"><a @click="emit('done')">已完成的需求</a></li>
      </ul>
    </div>
    <div class="demand-main">
      <div class="demand-item" v-for="i in 2">
        <div class="item-tile">
          <span class="time">提交时间：2018-07-07  15:43:23</span>
          <span class="status">状态</span>
        </div>
        <div class="item-cont">
          <span class="title">需求标题</span>
          <span class="city">重庆</span>
          <span class="cont">需求内容</span>
          <span class="company">对接的服务商</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "demand",
        props:{
          name
        },
        methods:{
          emit(a){
            this.bus.$emit('tab',a)
          }
        }
    }
</script>

<style scoped lang="stylus">
  #demand
    position relative
    width 880px
    height 670px
    border-radius 6px
    background-color white
    border 1px solid white
    .tab
      height 80px
      line-height 80px
      ul
        height 80px
        box-sizing border-box
        border-bottom 1px solid #ccc
        li
          height 80px
          box-sizing border-box
          display inline-block
          margin-left 50px
          font-size 16px
          color #555
          &.active
            border-bottom 2px solid #333
          a
            display block
    .demand-main
      padding 50px 11px
      .demand-item
        width 100%
        height 100px
        margin-bottom 5px
        border 1px solid #ccc
        .item-tile
          height 40px
          line-height 40px
          padding-left 20px
          padding-right 20px
          background-color #eaf8ff
          .time
            font-size 12px
            color #555
          .status
            float right
            font-size 12px
            color #555
        .item-cont
          height 60px
          line-height 60px
          padding 0 20px
          font-size 16px
          color #333
          .title
            margin-right 10px
            display inline-block
            width 200px
          .city
            margin-right 10px
            display inline-block
            width 100px
          .cont
            margin-right 10px
            display inline-block
            width 250px
          .company
            display inline-block
            width 200px
</style>



