<template>
  <div class="about">
    <div class="full-width business-banner" :style="{backgroundImage:`url(${img})`}">
    </div>
    <div class="container crumbs">
      您当前的位置 ：关于我们  >>  {{tab[tabActive]}}
    </div>
    <div class="full-width tab">
      <div class="container">
        <div class="row" style="width: 600px">
          <div class="row-item" v-for="(i,n) in tab">
            <a @click="tabActive=n" :class="{active:tabActive===n}">{{i}}</a>
          </div>
        </div>
      </div>
    </div>
    <transition-group name="fade" mode="out-in">
      <div class="full-width company-des" key="0" v-show="tabActive === 0">
        <div class="container">
          <div class="le">
            <div class="cont">
              <p>欧菲斯办公伙伴控股有限公司是全国最大的一站式办公整体解决方案提供商之一，拥有办公行业成熟且先进的服务标准，覆盖全国的高效服务网络。</p>
              <P>集团以O2O电商平台及创新现代服务业商业模式，实现行业核心资源的全面整合，通过打造全新政企需求生态链，以领先的5线3通电商化解决方案，满足客户从办公物资到办公非物资的全方位需求。</P>
              <P>25年发展历程未曾改变服务基因，一直秉承“您专心工作，琐事我来做”的服务理念，愿为更多客户带去最后一公里有温度的服务。</P>
            </div>
            <div class="le-row">
              <img style="margin-right: 20px" src="/static/about/aboutUs-company-1.png" alt="">
              <img src="/static/about/aboutUs-company-2.png" alt="">
            </div>
          </div>
          <div class="ri">
            <img src="/static/about/aboutUs-company-3.png" alt="">
          </div>
        </div>
      </div>
      <div class="full-width  company-cha" key="1" v-show="tabActive === 1">
        <div class="container">
          <img src="/static/about/aboutus.jpg" alt="">
        </div>
      </div>
      <div class="full-width company-len" key="2" v-show="tabActive === 2">
        <div class="container">
          <div class="detail-box">
            <div class="ph-item" v-for="i in info">
              <img :src="i.ico" alt="">
              <p class="name">{{i.ti}}</p>
              <p>{{i.ph}}</p>
            </div>
          </div>
          <div class="qc-code">
            <img src="/static/about/aboutUs-ewm.png" alt="">
            <p>增值通公众号</p>
          </div>
        </div>
      </div>
    </transition-group>
  </div>
</template>

<script>

    export default {
        name: "about",
        data(){
          return{
            tabActive:0,
            img:'/static/detail/3.png',
            tab:['公司介绍','发展历程','联系我们'],
            info:[
              {ti:'咨询热线',ph:'400-678-8616',ico:'/static/about/aboutUs-contactUs-icon-phone.png'},
              {ti:'市场合作',ph:'wanggui@officemate.cn',ico:'/static/about/aboutUs-contactUs-icon-cooperation.png'},
              {ti:'招商加盟',ph:'wanggui@officemate.cn',ico:'/static/about/aboutUs-contactUs-icon-jion.png'},
              {ti:'公司地址',ph:'重庆市渝中区经纬大道333号2幢13A层',ico:'/static/about/aboutUs-contactUs-icon-address.png'}
            ]
          }
        },
        created(){
          let data = {
            top:true,
            nav:true,
            foot:true
          };
          this.$store.dispatch('layout/setLayout', data);
        }
    }
</script>

<style scoped lang="stylus">
  .full-width.business-banner
    height 600px
    background center no-repeat
    background-size cover
    margin-top 1px
  .container.crumbs
    margin-top 50px
    color black
  .full-width.tab{
     margin-top: 48px;
     height: 40px;
     border-bottom: #ccc 1px solid;
  }
  .about .row{
    margin: 0 auto;
    position: relative;
  }
  .row-item{
    float: left;
    height: 40px;
    width 33.3333333%;
    text-align: center;
    font-size: 18px;
  }
  .row-item a{
    padding: 0 4px 20px;
    height: 40px;
    width 100px
    display: block;
    margin 0 auto
    font-weight: bold;
  }
  .row-item a.active{
    color: #038aeb;
    border-bottom: 3px solid #038aeb;
  }
  .company-cha
    .container
      padding 89px 10px 200px
  .company-des
    background-color #f2f2f2
    padding 98px 10px 200px
    &:after
      display block
      content ''
      clear both
    .le
      width 580px
      margin-right 20px
      float left
      .cont
        text-indent 2em
        line-height 29px
        font-size 16px
        padding 0 5px
        vertical-align top
      .le-row
        margin-top 22px
        img
          display block
          float left
    .ri
      width 580px
      float left
  .company-len
    .container
      padding 119px 10px 150px
      .detail-box
        position relative
        width 700px
        margin-left 80px
        &:after
          display block
          content ''
          clear both
        .ph-item
          display block
          position relative
          text-align center
          width 280px
          height 100px
          float left
          margin-right 70px
          margin-bottom 54px
          border solid 1px #038aeb
          border-radius 10px
          img
            position absolute
            top -30px
            left 111px
          p.name
            font-size 16px
            margin-top 40px
            margin-bottom 7px
            color black
      .qc-code
        position: absolute;
        right: 50px;
        top: 150px;
        p
          text-align center
  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }
  .fade-enter, .fade-leave-to{
    opacity: 0;
  }
</style>



