export default {
  hasItem(a){
    if (window.localStorage.getItem(a)){
      return true
    }else{
      return false
    }
  },
  //添加Storage
  addStorage :(key,value) => {
    window.localStorage.setItem(key,JSON.stringify(value))
  },
  //获取Storage
  getStorage :(key) =>{
    let result =  JSON.parse(window.localStorage.getItem(key));
    return  result
  },
  //删除Storage
  removeStorage:(key) => {
    window.localStorage.removeItem(key)
  }
}



